            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12">
                  <v-text-field
                    v-model="editedDevice.name"
                    label="device name"
                    required
                    :rules="[() => !isExistingDevicevalue(editedDevice.name, 'name') || 'Name Existed', rules.required]"
                    @input="val => (editedDevice.name = conventionNaming(val, setting))"
                  />
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="closeDevice">Cancel</v-btn>
          <v-btn color="blue" text @click="saveDevice(editedDevice)" :disabled="!valid">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Import device wizard dialog-->
