                      hide-details
                    ></v-text-field>
                  </v-toolbar>
                </template>

                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon small color="error" @click="onClickDeleteImportDevice(item)" v-if="!isClickSaveImportDevice">
