                  >
                  <v-icon
                    small
                    color="nubeGreen"
                    v-if="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')"
                    >fa-check-circle-o</v-icon
                  >
                  <v-icon
                    small
                    color="info"
                    @click="saveDevice(item)"
                    v-if="isClickSaveImportDevice"
                    :disabled="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')"
                    >fa-refresh</v-icon
                  >
                </template>
                <template v-slot:[`item.name`]="{ item }">
                  <v-text-field
                    v-model="item.name"
                    required
                    :rules="[() => !isExistingDevicevalue(item.name, 'name') || 'Name Existed', rules.required]"
                    :disabled="isClickSaveImportDevice && isExistingDevicevalue(item._id, '_id')"
                    @input="val => (item.name = conventionNaming(val, setting))"
                  />
                </template>
              </v-data-table>
            </v-form>
            <v-btn @click="e1Device = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImportDevice" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Delete Device dialog -->
    <confirm-dialog
      :is-open="dialogDeleteDevice"
      :on-close="closeDeleteDevice"
      :on-confirm="confirmDeleteDevice"
      dialog-text="Do you want to delete the device ?"
    ></confirm-dialog>

    <!-- Import wizard dialog-->
    <v-dialog v-model="dialogImport">
      <v-toolbar flat>
        <v-toolbar-title>Import Dialog</v-toolbar-title>
        <v-spacer></v-spacer>
        <v-btn icon @click="closeDialogImport">
          <v-icon>fa-times-circle</v-icon>
        </v-btn>
      </v-toolbar>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step :complete="e1 > 1" step="1"> Import </v-stepper-step>
          <v-divider></v-divider>
          <v-stepper-step :complete="e1 > 2" step="2"> Edit </v-stepper-step>
        </v-stepper-header>
        <v-stepper-items>
          <v-stepper-content step="1">
            <v-card class="mb-4">
              <v-card-text>
                <v-container>
                  <v-textarea outlined name="input-7-4" label="import data" v-model="dataImport"></v-textarea>
                </v-container>
              </v-card-text>
            </v-card>
            <v-btn color="primary" @click="clickContinue()" :disabled="!dataImport"> Continue </v-btn>
          </v-stepper-content>

          <v-stepper-content step="2">
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-data-table
                :headers="advancedHeaders"
                :items="importArray"
                class="elevation-1 mb-4"
                item-key="uuid"
                :footer-props="tableProps.FOOTER_PROPS"
                :itemsPerPage="tableProps.ITEMS_PER_PAGE"
                :search="searchWizard"
              >
                <template v-slot:top>
                  <v-toolbar flat>
                    <v-toolbar-title>IMPORT TABLE</v-toolbar-title>
                    <v-spacer></v-spacer>
                    <v-text-field
                      v-model="searchWizard"
                      append-icon="fa-search"
                      label="Search"
                      single-line
                      hide-details
                    ></v-text-field>
                  </v-toolbar>
                </template>
                <template v-slot:[`item.actions`]="{ item }">
                  <v-icon small color="error" @click="onClickDeleteImport(item)"> fa-trash-o </v-icon>
                  <v-icon small color="nubeGreen" v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')"
                    >fa-check-circle-o</v-icon
                  >
                  <v-icon
                    small
                    color="info"
                    @click="createPoint(item)"
                    v-if="isClickSaveImport"
                    :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')"
                    >fa-refresh</v-icon
                  >
                </template>
                <template v-slot:[`item.name`]="{ item }">
                  <v-text-field
                    v-model="item.name"
                    style="width: 100px"
                    required
                    :rules="[() => !isExistingvalue(item.name, 'name') || 'Name Existed', rules.required]"
                    :disabled="isClickSaveImport && isExistingvalue(item.uuid, 'uuid')"
                    @input="val => (item.name = conventionNaming(val, setting))"
                  />
                </template>
                <template v-slot:[`item.register`]="{ item }">
                  <v-text-field
                    v-if="isClickSaveImport && isExistingvalue(item.uuid, 'uuid', item)"
                    v-model="item.register"
                    single-line
                    style="width: 100px"
                    disabled
                  ></v-text-field>
                  <v-text-field
                    v-else
                    v-model="item.register"
                    type="number"
                    single-line
                    style="width: 100px"
                    required
                    :rules="[
                      () => !isExistingvalue(item.register, 'register', item) || 'Register Existed',
                      rules.required,
                    ]"
                  ></v-text-field>
                </template>
                <template v-slot:[`item.enable`]="{ item }">
                  <v-checkbox v-model="item.enable"></v-checkbox>
                </template>
                <template v-slot:[`item.function_code`]="{ item }">
                  <v-select
                    v-model="item.function_code"
                    :items="point_types"
                    required
                    :rules="[rules.required]"
                    v-on:change="$refs.form.resetValidation()"
                  ></v-select>
                </template>
                <template v-slot:[`item.value`]="{ item }">
                  <v-text-field v-model="item.value" type="number" single-line style="width: 100px"></v-text-field>
                </template>
              </v-data-table>
            </v-form>
            <v-btn color="secondary" @click="e1 = 1"> Back </v-btn>
            <v-btn color="primary" @click="saveImport" :disabled="!valid"> Save </v-btn>
          </v-stepper-content>
        </v-stepper-items>
      </v-stepper>
    </v-dialog>

    <!-- Import Excel dialog-->
    <import-from-excel-dialog
      :is-open="dialogImportExcel"
      :on-close="() => (dialogImportExcel = false)"
      :on-confirm="convertExcelToJson"
    ></import-from-excel-dialog>

    <!-- Delete dialog -->
    <confirm-dialog
      :is-open="dialogDelete"
      :on-close="closeDelete"
      :on-confirm="confirm"
      dialog-text="Do you want to delete ?"
    ></confirm-dialog>

    <!--Create/Edit dialog-->
    <v-dialog v-model="dialog" max-width="700px">
      <v-card>
        <v-card-title>
          <span class="headline">Point</span>
        </v-card-title>
        <v-card-text>
          <v-container>
            <v-form v-model="valid" ref="form" lazy-validation>
              <v-row>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field v-model="currentDevice.name" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field
                    v-model="editedItem.name"
                    label="Point name"
                    required
                    :rules="[() => !isExistingvalue(editedItem.name, 'name') || 'Name Existed', rules.required]"
                    @input="val => (editedItem.name = conventionNaming(val, setting))"
                  />
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.enable" label="enable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.writable" label="writable"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-checkbox v-model="editedItem.write_value_once" label="Write Value Once"></v-checkbox>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.cov_threshold" label="cov threshold" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select
                    v-model="editedItem.function_code"
                    label="function_code"
                    :items="point_types"
                    required
                    :rules="[rules.required]"
                    v-on:change="$refs.form.resetValidation()"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select
                    v-model="editedItem.data_type"
                    label="data type"
                    :items="point_data_types"
                    required
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-select
                    v-model="editedItem.data_endian"
                    label="data endian"
                    :items="point_data_endians"
                    required
                    :rules="[rules.required]"
                  ></v-select>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field
                    v-model="editedItem.register"
                    label="register"
                    :rules="[() => !isExistingvalue(editedItem, 'register') || 'Register Existed']"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field
                    v-model="editedItem.register_length"
                    label="register length"
                    type="number"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field value="Point round/math" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value" label="value" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_round" label="value round" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_operation" label="math operation"> </v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.value_raw" label="value raw"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="6">
                  <v-text-field
                    v-model="editedItem.fallback_value"
                    label="Fallback Value"
                    type="number"
                    hint="The empty value will return null"
                  ></v-text-field>
                </v-col>
                <v-col cols="12" sm="24" md="24">
                  <v-text-field value="Point value scale" disabled class="centered-input"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.input_min" label="input_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.input_max" label="input_max" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.scale_min" label="scale_min" type="number"></v-text-field>
                </v-col>
                <v-col cols="12" sm="6" md="3">
                  <v-text-field v-model="editedItem.scale_max" label="scale_max" type="number"></v-text-field>
                </v-col>
              </v-row>
            </v-form>
          </v-container>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="close"> Cancel </v-btn>
          <v-btn color="blue" text @click="save" :disabled="!valid"> Save </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" width="350px">
      <v-card>
        <v-card-title>Mass Edit</v-card-title>
        <v-card-text>
          <v-select v-if="editField === 'function_code'" v-model="editAll[editField]" :items="point_types"></v-select>
          <v-select
            v-if="editField === 'data_type'"
            v-model="editAll[editField]"
            :items="point_data_types"
            :label="toUpperTextKey(editField)"
          ></v-select>
          <v-select
            v-if="editField === 'data_endian'"
            v-model="editAll[editField]"
            :items="point_data_endians"
            :label="toUpperTextKey(editField)"
          ></v-select>
          <v-checkbox
            v-if="typeof editAll[editField] === 'boolean'"
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          />
          <v-text-field
            v-if="typeof editAll[editField] === 'number'"
            v-model.number="editAll[editField]"
            min="0"
            :label="toUpperTextKey(editField)"
            type="number"
            @input="numberChange()"
          ></v-text-field>
          <v-text-field
            v-if="
              typeof editAll[editField] === 'string' &&
              editField !== 'function_code' &&
              editField !== 'data_type' &&
              editField !== 'data_endian'
            "
            v-model="editAll[editField]"
            :label="toUpperTextKey(editField)"
          ></v-text-field>
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="darken-1" text @click="dialogMassEdit = false">Close</v-btn>
          <v-btn color="primary" text @click="saveMassEdit(editAll[editField], editField)">Save</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from '../../axios';
import * as xlsx from 'xlsx';
import ConfirmDialog from '../Common/ConfirmDialog';
import ImportFromExcelDialog from '../Common/ImportFromExcelDialog';
import { DATA_TABLE_PROPS } from '../../constants/appConstant';
import {
  RULES,
  POINT_DEFAULT,
  POINT_HEADERS,
  POINT_ADVANCED_HEADERS,
  POINT_TYPES,
  POINT_DATA_TYPES,
  POINT_DATA_ENDIANS,
  HISTORY_DATA_TYPES,
  POINT_EDIT_ALL_DEFAULT,
} from '../Modbus/modbusConstant';
import {
  copyToClipboard,
  isJsonString,
  exportJSON,
  exportCSV,
  exportExcel,
  toUpperTextKey,
  convertPointsValueToString,
  conventionNaming,
} from '../../utils/utils';

export default {
  components: {
    ConfirmDialog,
    ImportFromExcelDialog,
  },
  data: () => ({
    toUpperTextKey: toUpperTextKey,
    conventionNaming: conventionNaming,
    devices: [],
    importArrayDevice: [],
    expansionData: [],
    deviceHeaders: [
      { text: 'Actions', value: 'actions' },
      { text: 'Name', value: 'name', align: 'start' },
    ],
    searchWizardDevice: '',
    dataImportDevices: '',
    valid: false,
    dialogDevice: false,
    dialogDeleteDevice: false,
    dialogImportDevice: false,
    isClickSaveImportDevice: false,
    e1Device: 1,
    editedDeviceIndex: -1,
    editedDevice: {},
    currentDevice: {},
    rules: { ...RULES },
    advancedView: false,
    pointHeaders: [...POINT_HEADERS],
    advancedHeaders: [...POINT_ADVANCED_HEADERS],
    point_types: [...POINT_TYPES],
    point_data_types: [...POINT_DATA_TYPES],
    point_data_endians: [...POINT_DATA_ENDIANS],
    history_data_types: [...HISTORY_DATA_TYPES],
    editedItem: { ...POINT_DEFAULT },
    editedIndex: -1,
    selected: [],
    search: '',
    searchItems: [],
    action: '',
    dialog: false,
    dialogDelete: false,
    searchWizard: '',
    dataImport: '',
    importArray: [],
    dialogImport: false,
    e1: 1,
    dialogImportExcel: false,
    isClickSaveImport: false,
    editAll: { ...POINT_EDIT_ALL_DEFAULT },
    editField: '',
    dialogMassEdit: false,
    tableProps: { ...DATA_TABLE_PROPS },
  }),
  computed: {
    formDeviceTitle() {
      return this.editedDeviceIndex === -1 ? 'Create Device' : 'Edit Device';
    },
    setting() {
      return this.$store.state.setting;
    },
  },
  watch: {
    devices(val, oldVal) {
      if (val != oldVal) {
        this.expansionData = [...val.keys()].map((k, i) => i);
      }
    },
  },
  mounted() {
    this.getdevices();
  },
  methods: {
    //devices
    getdevices() {
      axios.get('modbus').then(r => {
        this.devices = r;
        this.devices.forEach(device => {
          if (device.points) {
            device.points = device.points.map(item => {
              if (item.point_store) {
                const keys = Object.keys(item.point_store);
                if (keys && keys.length > 0) {
                  keys.forEach(key => {
                    item = { ...item, [key]: item.point_store[key] };
                  });
                }
                delete item.point_store;
                delete item.point_id;
              }
              return item;
            });
          }
        });
      });
    },
    async clickJsonExportDevices() {
      const status = copyToClipboard(JSON.stringify(this.devices));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickImportJsonDevices() {
      this.dialogImportDevice = true;
      this.dataImportDevices = '';
      this.e1Device = 1;
    },
    async onClickDeleteImportDevice(item) {
      const index = this.importArrayDevice.indexOf(item);
      this.importArrayDevice.splice(index, 1);
    },
    async saveImportDevice() {
      if (this.importArrayDevice && this.importArrayDevice.length > 0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImportDevice = true;
          try {
            const promises = [];
            for (const item of this.importArrayDevice) {
              promises.push(new Promise((resolve, reject) => this.saveDevice(item, resolve, reject)));
            }
            await Promise.all(promises);
            this.closeDialogImportDevice();
          } catch (ex) {}
        }
      }
    },
    closeDialogImportDevice() {
      this.dialogImportDevice = false;
      this.isClickSaveImportDevice = false;
      this.$nextTick(() => {
        this.dataImportDevices = '';
      });
    },
    clickContinueImportDevice() {
      if (!isJsonString(this.dataImportDevices)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArrayDevice = JSON.parse(this.dataImportDevices).map(device => {
        delete device._id;
        return device;
      });
      this.e1Device = 2;
    },
    isExistingDevicevalue(value, fieldName) {
      const devices = [...this.devices];
      if (this.editedDeviceIndex !== -1) {
        //when edit
        devices.splice(this.editedDeviceIndex, 1);
      }
      return devices.find(d => d[fieldName] == value) != null;
    },
    closeDevice() {
      this.dialogDevice = false;
      this.$nextTick(() => {
        this.editedDevice = Object.assign({}, {});
        this.editedDeviceIndex = -1;
      });
    },
    async saveDevice(data, resolve, reject) {
      try {
        const method = data._id ? 'put' : 'post';
        const points = data.points ? data.points : [];
        delete data.points;
        const response = await axios[method]('modbus', data);
        if (data._id) {
          Object.assign(this.devices[this.editedDeviceIndex], response);
          this.$toasted.show('update success');
        } else {
          this.devices.push(response);
          this.$toasted.show('create success');
          if (points.length > 0) {
            for (const point of points) {
              point.device_id = response._id;
              point.register = point.register ? point.register : 0;
              point.register_length = point.register_length ? point.register_length : 0;
              point.enable = point.enable ? point.enable : false;
              delete point._id;
              this.createPoint(point);
            }
          }
        }
        if (this.importArrayDevice && this.importArrayDevice.length > 0) {
          const index = this.importArrayDevice.findIndex(n => n.name == response.name);
          if (index > -1) {
            Object.assign(this.importArrayDevice[index], response);
          }
        }
        this.closeDevice();
        if (resolve) {
          resolve();
        }
      } catch (e) {
        if (reject) {
          reject();
        }
      }
    },
    async onClickDeleteDevice(item) {
      this.dialogDeleteDevice = true;
      this.currentDevice = item;
    },
    closeDeleteDevice() {
      this.dialogDeleteDevice = false;
      this.$nextTick(() => {
        this.currentDevice = {};
      });
    },
    async confirmDeleteDevice() {
      const response = await axios.delete(`modbus/${this.currentDevice._id}`);
      if (response) {
        const index = this.devices.indexOf(this.currentDevice);
        this.devices.splice(index, 1);
        this.closeDeleteDevice();
      }
    },
    clickEditDevice(item) {
      this.editedDeviceIndex = this.devices.indexOf(item);
      this.editedDevice = Object.assign({}, item);
      this.dialogDevice = true;
    },
    onClickRefresh() {
      this.getdevices();
      this.$toasted.show(`successfully refresh`, { type: 'success' });
    },
    //end devices

    //point
    async onClickDelete(item) {
      this.action = 'deleteSingle';
      this.dialogDelete = true;
      this.editedItem = item;
    },
    async onClickDeleteMulti(device) {
      this.currentDevice = device;
      this.dialogDelete = true;
      this.action = 'deleteSelected';
    },
    async deleteSingle() {
      await axios.delete(`modbus/point/${this.editedItem._id}`);
      this.editedDeviceIndex = this.devices.indexOf(this.currentDevice);
      this.editedIndex = this.devices[this.editedDeviceIndex].points.indexOf(this.editedItem);
      this.devices[this.editedDeviceIndex].points.splice(this.editedIndex, 1);
      this.closeDelete();
    },
    async deleteSelected() {
      const promises = [];
      for (const item of this.selected) {
        this.editedItem = item;
        promises.push(this.deleteSingle());
      }
      await Promise.all(promises);
      this.selected = [];
    },
    async confirm() {
      await this[this.action]();
      this.dialog = false;
      this.action = '';
    },
    closeDelete() {
      this.dialogDelete = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...POINT_DEFAULT });
        this.editedIndex = -1;
      });
    },
    save() {
      if (this.$refs.form.validate()) {
        if (this.editedItem._id) {
          this.updatePoint(this.editedItem);
        } else {
          this.createPoint(this.editedItem);
        }
      }
    },
    async updatePoint(point) {
      const response = await axios.put('modbus/point', point);
      const { deviceIndex, pointIndex } = this.getIndexs(point);
      Object.assign(this.devices[deviceIndex].points[pointIndex], response);
      this.$toasted.show('update success');
      this.close();
    },
    async createPoint(point) {
      const response = await axios.post('modbus/point', point);
      const { deviceIndex } = this.getIndexs(point);
      this.devices[deviceIndex].points = this.devices[deviceIndex].points ? [...this.devices[deviceIndex].points] : [];
      this.devices[deviceIndex].points.push(response);
      this.$toasted.show('create success');
      this.close();
    },
    async saveImport() {
      if (this.importArray && this.importArray.length > 0) {
        if (this.$refs.form.validate()) {
          this.isClickSaveImport = true;
          try {
            for (const item of this.importArray) {
              item.device_id = this.currentDevice._id; //update to the current device_id
              item.register_length = item.register_length ? item.register_length : 0;
              if (!item._id) {
                this.createPoint(item);
              }
            }
            this.closeDialogImport();
            this.selected = [];
          } catch (ex) {}
        }
      }
    },
    closeDialogImport() {
      this.dialogImport = false;
      this.isClickSaveImport = false;
      this.$nextTick(() => {
        this.dataImport = '';
      });
    },
    async onClickDeleteImport(item) {
      const index = this.importArray.indexOf(item);
      this.importArray.splice(index, 1);
    },
    clickImportJson(device) {
      this.dialogImport = true;
      this.dataImport = this.selected.length > 0 ? JSON.stringify(this.selected) : '';
      this.e1 = 1;
      this.editedDeviceIndex = this.devices.indexOf(device);
      this.currentDevice = device;
    },
    clickImportExcel(device) {
      this.dialogImportExcel = true;
      this.editedDeviceIndex = this.devices.indexOf(device);
      this.currentDevice = device;
    },
    convertExcelToJson(file) {
      let fileReader = new FileReader();
      fileReader.readAsBinaryString(file);
      fileReader.onload = event => {
        let data = event.target.result;
        let workbook = xlsx.read(data, { type: 'binary' });
        // workbook.SheetNames.forEach(sheet => {
        let rowObject = xlsx.utils.sheet_to_row_object_array(workbook.Sheets['Sheet1']);
        const items = rowObject.map(i => {
          for (let key in i) {
            const newKey = key.trimRight('');
            const value = i[key];
            delete i[key];
            i = { ...i, [newKey]: value };
          }
          i.device_id = this.currentDevice._id;
          return i;
        });
        this.dataImport = JSON.stringify(items);
        this.dialogImportExcel = false;
        this.dialogImport = true;
        this.e1 = 1;
        // });
      };
    },
    clickContinue() {
      if (!isJsonString(this.dataImport)) {
        return this.$toasted.show('incorrect data type!', { type: 'error' });
      }
      this.importArray = JSON.parse(this.dataImport).map(point => {
        delete point._id;
        return point;
      });
      this.e1 = 2;
    },
    isExistingvalue(value, fieldName, item) {
      if (!this.devices || (this.devices && this.devices.length == 0)) return false;
      const deviceIndex = this.devices.indexOf(this.currentDevice);
      let points = this.devices[deviceIndex].points ? [...this.devices[deviceIndex].points] : [];
      if (points && points.length == 0) return false;
      if (this.editedIndex !== -1) {
        //when edit
        points.splice(this.editedIndex, 1);
      }
      if (fieldName === 'register') {
        value = Number(value);
        if (item) {
          points = points.filter(p => p.function_code === item.function_code);
        }
      }
      return points.find(p => p[fieldName] == value) != null;
    },
    clickJsonExport() {
      const status = copyToClipboard(JSON.stringify(this.selected));
      const msg = status ? `Copied to clipboard!` : `Failure on copying on clipboard!`;
      const type = status ? `success` : `error`;
      return this.$toasted.show(msg, { type: type });
    },
    clickDownload(array, type) {
      const data = convertPointsValueToString(JSON.parse(JSON.stringify(array)));
      if (type === 'csv') {
        return exportCSV('pre_made_points', data);
      }
      if (type === 'excel') {
        return exportExcel('pre_made_points', data);
      }
      if (type === 'json') {
        return exportJSON('pre_made_points', JSON.stringify(array));
      }
    },
    onClickCreateEditPoint(item) {
      this.editedDeviceIndex = this.devices.indexOf(this.currentDevice);
      this.editedIndex = this.devices[this.editedDeviceIndex].points.indexOf(item);
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },
    saveMassEdit(value, field) {
      for (const item of this.selected) {
        item[field] = value;
        this.updatePoint(item);
      }
      this.refresh();
    },
    async openMassEditDialog(fieldName) {
      if (this.selected.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
    },
    refresh() {
      this.editAll = {
        enable: false,
        register_length: 0,
        function_code: 'READ_HOLDING_REGISTERS',
        data_type: 'RAW',
        data_endian: 'BEB_LEW',
        value_round: 1,
        value_offset: 0,
        value_operation: 'x + 0',
        writable: false,
        cov_threshold: 0,
        history_enable: false,
        history_type: 'INTERVAL',
        history_interval: 15,
        input_min: 0,
        input_max: 0,
        scale_min: 0,
        scale_max: 0,
        value: 0,
      };
      this.dialogMassEdit = false;
    },
    numberChange() {
      if (!this.editAll[this.editField]) return (this.editAll[this.editField] = 0);
    },
    cancelEdit() {},
    openEdit(item) {
      this.editedItem = Object.assign({}, item);
    },
    closeEdit() {
      this.editedIndex = -1;
      this.editedItem = {};
    },
    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, { ...POINT_DEFAULT });
        this.editedIndex = -1;
      });
    },

    getIndexs(point) {
      const deviceIndex = this.devices ? this.devices.findIndex(d => d._id == point.device_id) : -1;
      const pointIndex =
        deviceIndex > -1 && this.devices[deviceIndex].points
          ? this.devices[deviceIndex].points.findIndex(p => p._id == point._id)
          : -1;
      return { deviceIndex, pointIndex };
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },

    //end point
  },
};
</script>




